'use client';

import { useRouter } from 'next/navigation';
import { useState, useEffect } from 'react';
import { Play, Pause, Disc3, ArrowRight, Activity, Loader2, Music, CheckCircle2 } from 'lucide-react';

// Mock data for the generated tracks
const MOCK_TRACKS = [
    {
        id: 'track-1',
        title: 'Sombras en la Calle',
        style: 'Dark Trap, 808s, Moody',
        duration: '2:45',
        image: 'bg-gradient-to-br from-indigo-900 to-black',
        tags: ['Trap', 'Dark']
    },
    {
        id: 'track-2',
        title: 'Luces de Neón',
        style: 'Dark Trap, Melodic, Heavy Bass',
        duration: '3:10',
        image: 'bg-gradient-to-br from-purple-900 to-black',
        tags: ['Trap', 'Melodic']
    }
];

export default function Crear() {
    const router = useRouter();
    const [isGenerating, setIsGenerating] = useState(true);
    const [tracks, setTracks] = useState<any[]>([]);
    const [selectedTrack, setSelectedTrack] = useState<string | null>(null);
    const [playingTrack, setPlayingTrack] = useState<string | null>(null);

    useEffect(() => {
        const triggerGeneration = async () => {
            try {
                // In a real scenario, we'd pass the actual prompt from the previous step
                const response = await fetch('/api/ai/generate', {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({
                        prompt: 'Salsa hit style Grupo Niche',
                        duration: 120,
                        style: 'Salsa',
                        instrument: 'Trombones'
                    })
                });

                const data = await response.json();
                if (data.tracks) {
                    setTracks(data.tracks);
                } else {
                    // Fallback to mock if API returns nothing but success
                    setTracks(MOCK_TRACKS);
                }
            } catch (error) {
                console.error('Generation Error:', error);
                setTracks(MOCK_TRACKS); // Fallback
            } finally {
                setIsGenerating(false);
            }
        };

        triggerGeneration();
    }, []);

    useEffect(() => {
        const audio = document.getElementById('preview-audio') as HTMLAudioElement;
        if (audio) {
            if (playingTrack) {
                const track = tracks.find(t => t.id === playingTrack);
                if (track && track.url) {
                    audio.src = track.url;
                    audio.play().catch(e => console.error('Playback Error:', e));
                }
            } else {
                audio.pause();
                audio.src = '';
            }
        }
    }, [playingTrack, tracks]);

    const handleSendToStudio = () => {
        if (!selectedTrack) return;
        router.push('/studio');
    };

    return (
        <div className="flex flex-col h-full w-full items-center py-12 px-8">
            <audio id="preview-audio" className="hidden" />

            {/* TOP HEADER */}
            <div className="flex flex-col items-center justify-center opacity-90 mix-blend-screen mb-12">
                <div className="border border-cyan-glow/30 px-6 py-2 rounded-sm bg-black/50 backdrop-blur-md shadow-[0_0_15px_rgba(0,240,255,0.1)]">
                    <h1 className="text-2xl font-sans tracking-widest text-[#E0E0E0]">
                        DA GRABA <span className="font-light">CREATOR IA</span>
                    </h1>
                </div>
            </div>

            {/* MAIN CONTENT AREA */}
            <div className="w-full max-w-5xl flex flex-col gap-8">

                {/* GENERATION STATE */}
                <div className="glass-panel w-full p-8 rounded-xl border border-white/5 shadow-2xl bg-[#0B1015]/80 flex flex-col items-center relative overflow-hidden">
                    {/* Subtle background glow */}
                    <div className="absolute top-0 right-1/4 w-64 h-64 bg-cyan-glow/10 rounded-full blur-[100px] pointer-events-none" />

                    {isGenerating ? (
                        <div className="flex flex-col items-center gap-6 py-12">
                            <div className="relative flex items-center justify-center">
                                {/* Intense rotating loaders */}
                                <div className="absolute inset-0 w-32 h-32 border-4 border-t-cyan-glow border-r-transparent border-b-cyan-glow/20 border-l-transparent rounded-full animate-spin glow-cyan" />
                                <div className="absolute inset-0 w-32 h-32 border-4 border-t-transparent border-r-orange-500 border-b-transparent border-l-orange-500/20 rounded-full animate-spin-slow glow-orange" style={{ animationDirection: 'reverse' }} />
                                {/* Center Icon */}
                                <Music size={40} className="text-white animate-pulse" />
                            </div>
                            <div className="flex flex-col items-center gap-2">
                                <h2 className="text-xl font-bold tracking-widest text-[#E0E0E0] flex items-center gap-2">
                                    <Activity size={18} className="text-cyan-glow" />
                                    JOHN ESTÁ COCINANDO
                                </h2>
                                <p className="text-silver-dark text-sm font-mono tracking-wide text-center max-w-md">
                                    Generando instrumentales, estructurando tu idea y renderizando vocales con IA avanzada...
                                </p>
                            </div>
                        </div>
                    ) : (
                        <div className="w-full flex justify-between items-center">
                            <div>
                                <h2 className="text-2xl font-bold tracking-widest text-white flex items-center gap-3">
                                    <CheckCircle2 className="text-green-500" />
                                    GENERACIÓN COMPLETADA
                                </h2>
                                <p className="text-silver-dark text-sm mt-1">John ha preparado {tracks.length} variantes basadas en tu estilo.</p>
                            </div>

                            {/* Actions Button */}
                            <button
                                onClick={handleSendToStudio}
                                disabled={!selectedTrack}
                                className={`flex items-center gap-2 px-6 py-3 rounded-full font-bold tracking-widest transition-all duration-300 shadow-xl ${selectedTrack
                                    ? 'bg-gradient-to-r from-orange-600 to-primary text-white hover:scale-105 shadow-[0_0_20px_rgba(255,107,0,0.5)] glow-orange'
                                    : 'bg-[#222] text-silver-dark opacity-50 cursor-not-allowed'
                                    }`}
                            >
                                IR AL STUDIO DE MEZCLA
                                <ArrowRight size={18} />
                            </button>
                        </div>
                    )}
                </div>

                {/* TRACKS LIST */}
                <div className={`transition-all duration-1000 ${isGenerating ? 'opacity-30 pointer-events-none translate-y-8' : 'opacity-100 translate-y-0'}`}>
                    <h3 className="text-silver-light font-mono tracking-widest mb-4 flex items-center gap-2">
                        <Disc3 size={16} className="text-cyan-glow" />
                        RESULTADOS
                    </h3>

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        {tracks.map((track) => (
                            <div
                                key={track.id}
                                onClick={() => setSelectedTrack(track.id)}
                                className={`flex flex-col group cursor-pointer transition-all duration-300 rounded-xl overflow-hidden border ${selectedTrack === track.id
                                    ? 'border-cyan-glow bg-cyan-glow/10 shadow-[0_0_20px_rgba(0,240,255,0.15)] glow-cyan transform scale-[1.02]'
                                    : 'border-white/5 bg-[#111720]/80 hover:bg-[#1A2330] hover:border-white/20'
                                    }`}
                            >
                                {/* Track Header Card */}
                                <div className="flex items-center gap-4 p-4 border-b border-white/5">
                                    {/* Cover Art - Suno Style */}
                                    <div className={`w-20 h-20 rounded-md shadow-inner flex items-center justify-center relative overflow-hidden bg-[#111]`}>
                                        {track.image ? (
                                            <img src={track.image} alt={track.title} className="w-full h-full object-cover" />
                                        ) : (
                                            <div className="w-full h-full bg-gradient-to-br from-indigo-900 to-black" />
                                        )}
                                        <div className="absolute inset-0 bg-black/40 opacity-0 group-hover:opacity-100 transition-opacity flex items-center justify-center">
                                            <button
                                                className="w-12 h-12 rounded-full bg-white/20 backdrop-blur-md flex items-center justify-center hover:bg-white/40 transition-colors"
                                                onClick={(e) => {
                                                    e.stopPropagation();
                                                    setPlayingTrack(playingTrack === track.id ? null : track.id);
                                                }}
                                            >
                                                {playingTrack === track.id ? <Pause fill="white" size={20} /> : <Play fill="white" size={20} className="ml-1" />}
                                            </button>
                                        </div>
                                    </div>

                                    {/* Track Info */}
                                    <div className="flex-1">
                                        <h4 className="text-white font-bold text-lg">{track.title}</h4>
                                        <p className="text-silver-dark text-xs">{track.style}</p>
                                    </div>

                                    {/* Duration */}
                                    <div className="text-silver-dark font-mono text-xs px-2">
                                        {track.duration || '3:00'}
                                    </div>
                                </div>

                                {/* Track Active Visualization (if selected) */}
                                <div className="h-1 bg-black w-full overflow-hidden">
                                    <div
                                        className={`h-full w-full bg-cyan-glow transition-transform duration-[30s] ease-linear transform origin-left ${playingTrack === track.id ? 'scale-x-100' : 'scale-x-0'}`}
                                    />
                                </div>

                                {/* Selection indicator bottom */}
                                <div className={`px-4 py-2 flex justify-between items-center text-xs transition-colors ${selectedTrack === track.id ? 'bg-cyan-glow/20 text-cyan-glow' : 'text-transparent'}`}>
                                    <span className="font-bold tracking-widest">SELECCIONADO</span>
                                    <CheckCircle2 size={14} />
                                </div>
                            </div>
                        ))}
                    </div>
                </div>

            </div>
        </div>
    );
}
